.h-2 {
	width: 100%;
	height: 100%;
	background-image: url('../assets/images/index/i-2.png');
	background-size: cover;
	background-position: center;
	.main-title {
		margin-bottom: 60px; }
	.left {
		width: 100%;
		display: flex;
		align-items: center;
		&__content {
			.block-title {
				margin-bottom: 43px; }
			&__desc {
				p {
					font-family: "Montserrat";
					font-size: 20px;
					color: #666666;
					text-align: left;
					padding-bottom: 32px;

					&:nth-child(1) {
						font-weight: bold; } } }
			.btn-detail {
				background-color: rgb(26, 119, 186);
				border: solid 1px transparent;
				.icon {
					&::before {
						content: url('../assets/icons/plus-hover.png'); } }
				&:hover {
					color: rgb(26, 119, 186);
					background-color: #fff;
					border: solid 1px rgb(26, 119, 186);
					.icon {
						&::before {
							content: url('../assets/icons/plus.png'); } } } } } }
	.right {
		width: 100%;
		height: 100%;
		&__content {
			width: 100%;
			.block-title {
				margin-bottom: 30px;
				padding-left: 73px;
				h2 {
					color: #ffffff; }
				&::before {
					background-color: #ffffff; } }
			&__img {
				width: 495px;
				height: 420px;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover; } } } }
	@media screen and ( max-width: 1600px) {
		.main-title {
			margin-top: 50px;
			margin-bottom: 40px; }
		.left {
			&__content {
				.block-title {
					margin-bottom: 20px; }
				&__desc {
					p {
						padding-bottom: 15px; } } } }
		.right {
			&__content {
				.block-title {
					padding-left: 40px; }
				&__img {
					width: 85%;
					height: 350px; } } } }
	@media screen and ( max-width: 1440px) {
		.main-title {
			margin-bottom: 30px; }
		.left {
			&__content {
				.block-title {
					margin-bottom: 30px; }
				&__desc {
					p {
						font-size: 16px;
						padding-bottom: 32px; } } } }
		.right {
			&__content {
				.block-title {
					padding-left: 25px; }
				&__img {
					width: 80%;
					height: 329px; } } } }
	@media screen and ( max-width: 1200px) {
		.main-title {
			margin-top: 0; }
		.left {
			&__content {
				.block-title {
					margin-bottom: 20px; }
				&__desc {
					p {
						padding-bottom: 15px; } } } }
		.right {
			&__content {
				.block-title {
					padding-left: 105px; } } } }
	@media screen and ( max-width: 768px) {
		background-color: rgba($color: #a2d8ff, $alpha: .5);
		background-image: none !important;
		.left {
			&__content {
				.block-title {
					margin-bottom: 15px; } } }
		.right {
			&__content {
				.block-title {
					padding-left: 0;
					margin-bottom: 15px;
					h2 {
						color: #333333; }
					&::before {
						background-color: rgb(26, 119, 185); } }
				&__img {
					width: 100%;
					height: auto; } } } }
	@media screen and ( max-width: 767px) {
		.main-title {
			margin-bottom: 20px; }
		.left {
			margin-bottom: 30px;
			&__content {
				.block-title {
					margin-bottom: 15px; }
				&__desc {
					p {
						padding-bottom: 10px; } } } } } }
